/**
 * Simulador de dados de wearables para o app MindFlow
 * Simula batimentos cardíacos e taxa de respiração
 */

export interface SensorData {
  heartRate: number;
  respirationRate: number;
  timestamp: Date;
  stressLevel: 'low' | 'medium' | 'high';
}

export interface BiometricTrend {
  average: number;
  trend: 'increasing' | 'decreasing' | 'stable';
  lastWeekAverage: number;
}

/**
 * Simula dados de sensores em tempo real
 */
export function generateSensorData(): SensorData {
  const baseHeartRate = 70;
  const baseRespirationRate = 16;
  
  // Adiciona variação natural aos dados
  const heartRate = Math.floor(baseHeartRate + (Math.random() - 0.5) * 20);
  const respirationRate = Math.floor(baseRespirationRate + (Math.random() - 0.5) * 6);
  
  // Determina nível de estresse baseado nos dados
  let stressLevel: 'low' | 'medium' | 'high' = 'low';
  if (heartRate > 85 || respirationRate > 20) {
    stressLevel = 'high';
  } else if (heartRate > 75 || respirationRate > 18) {
    stressLevel = 'medium';
  }
  
  return {
    heartRate: Math.max(50, Math.min(120, heartRate)),
    respirationRate: Math.max(10, Math.min(25, respirationRate)),
    timestamp: new Date(),
    stressLevel
  };
}

/**
 * Simula histórico de dados biométricos
 */
export function generateHistoricalData(days: number = 7): SensorData[] {
  const data: SensorData[] = [];
  const now = new Date();
  
  for (let i = days - 1; i >= 0; i--) {
    const date = new Date(now);
    date.setDate(date.getDate() - i);
    
    // Gera múltiplas leituras por dia
    for (let hour = 8; hour < 22; hour += 2) {
      date.setHours(hour, 0, 0, 0);
      data.push({
        ...generateSensorData(),
        timestamp: new Date(date)
      });
    }
  }
  
  return data;
}

/**
 * Calcula tendências biométricas
 */
export function calculateBiometricTrends(data: SensorData[]): {
  heartRate: BiometricTrend;
  respirationRate: BiometricTrend;
} {
  if (data.length === 0) {
    return {
      heartRate: { average: 70, trend: 'stable', lastWeekAverage: 70 },
      respirationRate: { average: 16, trend: 'stable', lastWeekAverage: 16 }
    };
  }
  
  const recentData = data.slice(-7);
  const olderData = data.slice(-14, -7);
  
  const recentHeartRateAvg = recentData.reduce((sum, d) => sum + d.heartRate, 0) / recentData.length;
  const olderHeartRateAvg = olderData.length > 0 
    ? olderData.reduce((sum, d) => sum + d.heartRate, 0) / olderData.length 
    : recentHeartRateAvg;
  
  const recentRespirationAvg = recentData.reduce((sum, d) => sum + d.respirationRate, 0) / recentData.length;
  const olderRespirationAvg = olderData.length > 0
    ? olderData.reduce((sum, d) => sum + d.respirationRate, 0) / olderData.length
    : recentRespirationAvg;
  
  const heartRateTrend = Math.abs(recentHeartRateAvg - olderHeartRateAvg) < 2 
    ? 'stable' 
    : recentHeartRateAvg > olderHeartRateAvg ? 'increasing' : 'decreasing';
    
  const respirationTrend = Math.abs(recentRespirationAvg - olderRespirationAvg) < 1
    ? 'stable'
    : recentRespirationAvg > olderRespirationAvg ? 'increasing' : 'decreasing';
  
  return {
    heartRate: {
      average: Math.round(recentHeartRateAvg),
      trend: heartRateTrend,
      lastWeekAverage: Math.round(olderHeartRateAvg)
    },
    respirationRate: {
      average: Math.round(recentRespirationAvg),
      trend: respirationTrend,
      lastWeekAverage: Math.round(olderRespirationAvg)
    }
  };
}

/**
 * Simula intervenção baseada nos dados dos sensores
 */
export function generateIntervention(sensorData: SensorData): {
  type: 'breathing' | 'meditation' | 'movement' | 'none';
  message: string;
  duration: number; // em minutos
} {
  const { stressLevel, heartRate, respirationRate } = sensorData;
  
  if (stressLevel === 'high') {
    if (respirationRate > 20) {
      return {
        type: 'breathing',
        message: 'Sua respiração está acelerada. Que tal fazer um exercício de respiração profunda?',
        duration: 5
      };
    }
    if (heartRate > 90) {
      return {
        type: 'meditation',
        message: 'Seus batimentos estão elevados. Uma meditação rápida pode ajudar a relaxar.',
        duration: 10
      };
    }
  }
  
  if (stressLevel === 'medium') {
    return {
      type: 'movement',
      message: 'Que tal uma caminhada leve para equilibrar suas emoções?',
      duration: 15
    };
  }
  
  return {
    type: 'none',
    message: 'Seus sinais vitais estão equilibrados. Continue assim!',
    duration: 0
  };
}
