"use client";

import React, { createContext, useContext, useReducer, useEffect, ReactNode } from 'react';
import { SensorData, generateSensorData, generateHistoricalData } from '@/lib/sensorSimulator';

// Tipos para o estado da aplicação
export interface EmotionalState {
  mood: number; // 1-10 scale
  energy: number; // 1-10 scale
  stress: number; // 1-10 scale
  timestamp: Date;
}

export interface Challenge {
  id: string;
  title: string;
  description: string;
  completed: boolean;
  points: number;
  category: 'mindfulness' | 'social' | 'physical' | 'cognitive';
}

export interface UserProgress {
  totalPoints: number;
  level: number;
  streak: number;
  completedChallenges: string[];
  badges: string[];
}

export interface AppState {
  // Estado emocional
  currentEmotionalState: EmotionalState;
  emotionalHistory: EmotionalState[];
  
  // Dados dos sensores
  currentSensorData: SensorData;
  sensorHistory: SensorData[];
  
  // Desafios e gamificação
  dailyChallenges: Challenge[];
  userProgress: UserProgress;
  
  // Configurações
  isFirstTime: boolean;
  premiumSubscription: boolean;
  
  // Estado da UI
  loading: boolean;
  error: string | null;
}

// Ações do reducer
type AppAction =
  | { type: 'UPDATE_EMOTIONAL_STATE'; payload: EmotionalState }
  | { type: 'UPDATE_SENSOR_DATA'; payload: SensorData }
  | { type: 'COMPLETE_CHALLENGE'; payload: string }
  | { type: 'ADD_POINTS'; payload: number }
  | { type: 'SET_LOADING'; payload: boolean }
  | { type: 'SET_ERROR'; payload: string | null }
  | { type: 'INITIALIZE_APP' }
  | { type: 'RESET_DAILY_CHALLENGES' };

// Estado inicial
const initialState: AppState = {
  currentEmotionalState: {
    mood: 7,
    energy: 6,
    stress: 4,
    timestamp: new Date()
  },
  emotionalHistory: [],
  currentSensorData: generateSensorData(),
  sensorHistory: generateHistoricalData(7),
  dailyChallenges: [],
  userProgress: {
    totalPoints: 0,
    level: 1,
    streak: 0,
    completedChallenges: [],
    badges: []
  },
  isFirstTime: true,
  premiumSubscription: false,
  loading: false,
  error: null
};

// Reducer
function appReducer(state: AppState, action: AppAction): AppState {
  switch (action.type) {
    case 'UPDATE_EMOTIONAL_STATE':
      return {
        ...state,
        currentEmotionalState: action.payload,
        emotionalHistory: [...state.emotionalHistory, action.payload].slice(-30) // Mantém últimos 30 registros
      };
      
    case 'UPDATE_SENSOR_DATA':
      return {
        ...state,
        currentSensorData: action.payload,
        sensorHistory: [...state.sensorHistory, action.payload].slice(-100) // Mantém últimos 100 registros
      };
      
    case 'COMPLETE_CHALLENGE':
      const challenge = state.dailyChallenges.find(c => c.id === action.payload);
      if (!challenge || challenge.completed) return state;
      
      const updatedChallenges = state.dailyChallenges.map(c =>
        c.id === action.payload ? { ...c, completed: true } : c
      );
      
      const newTotalPoints = state.userProgress.totalPoints + challenge.points;
      const newLevel = Math.floor(newTotalPoints / 100) + 1;
      
      return {
        ...state,
        dailyChallenges: updatedChallenges,
        userProgress: {
          ...state.userProgress,
          totalPoints: newTotalPoints,
          level: newLevel,
          completedChallenges: [...state.userProgress.completedChallenges, action.payload]
        }
      };
      
    case 'ADD_POINTS':
      const totalPoints = state.userProgress.totalPoints + action.payload;
      return {
        ...state,
        userProgress: {
          ...state.userProgress,
          totalPoints,
          level: Math.floor(totalPoints / 100) + 1
        }
      };
      
    case 'SET_LOADING':
      return { ...state, loading: action.payload };
      
    case 'SET_ERROR':
      return { ...state, error: action.payload };
      
    case 'INITIALIZE_APP':
      return {
        ...state,
        dailyChallenges: generateDailyChallenges(),
        isFirstTime: false
      };
      
    case 'RESET_DAILY_CHALLENGES':
      return {
        ...state,
        dailyChallenges: generateDailyChallenges()
      };
      
    default:
      return state;
  }
}

// Função para gerar desafios diários
function generateDailyChallenges(): Challenge[] {
  const challenges = [
    {
      id: '1',
      title: 'Respiração Consciente',
      description: 'Pratique 5 minutos de respiração profunda',
      completed: false,
      points: 10,
      category: 'mindfulness' as const
    },
    {
      id: '2',
      title: 'Gratidão Diária',
      description: 'Liste 3 coisas pelas quais você é grato hoje',
      completed: false,
      points: 15,
      category: 'cognitive' as const
    },
    {
      id: '3',
      title: 'Conexão Social',
      description: 'Envie uma mensagem carinhosa para alguém especial',
      completed: false,
      points: 20,
      category: 'social' as const
    },
    {
      id: '4',
      title: 'Movimento Consciente',
      description: 'Faça 10 minutos de alongamento ou caminhada',
      completed: false,
      points: 15,
      category: 'physical' as const
    }
  ];
  
  // Retorna 3 desafios aleatórios
  return challenges.sort(() => Math.random() - 0.5).slice(0, 3);
}

// Context
const AppContext = createContext<{
  state: AppState;
  dispatch: React.Dispatch<AppAction>;
} | null>(null);

// Provider
export function AppProvider({ children }: { children: ReactNode }) {
  const [state, dispatch] = useReducer(appReducer, initialState);
  
  // Atualiza dados dos sensores periodicamente
  useEffect(() => {
    const interval = setInterval(() => {
      dispatch({ type: 'UPDATE_SENSOR_DATA', payload: generateSensorData() });
    }, 30000); // A cada 30 segundos
    
    return () => clearInterval(interval);
  }, []);
  
  // Inicializa a aplicação
  useEffect(() => {
    if (state.isFirstTime) {
      dispatch({ type: 'INITIALIZE_APP' });
    }
  }, [state.isFirstTime]);
  
  return (
    <AppContext.Provider value={{ state, dispatch }}>
      {children}
    </AppContext.Provider>
  );
}

// Hook personalizado
export function useAppContext() {
  const context = useContext(AppContext);
  if (!context) {
    throw new Error('useAppContext deve ser usado dentro de um AppProvider');
  }
  return context;
}

// Funções auxiliares para ações comuns
export const useAppActions = () => {
  const { dispatch } = useAppContext();
  
  return {
    updateEmotionalState: (state: EmotionalState) => 
      dispatch({ type: 'UPDATE_EMOTIONAL_STATE', payload: state }),
    
    completeChallenge: (challengeId: string) => 
      dispatch({ type: 'COMPLETE_CHALLENGE', payload: challengeId }),
    
    addPoints: (points: number) => 
      dispatch({ type: 'ADD_POINTS', payload: points }),
    
    setLoading: (loading: boolean) => 
      dispatch({ type: 'SET_LOADING', payload: loading }),
    
    setError: (error: string | null) => 
      dispatch({ type: 'SET_ERROR', payload: error }),
    
    resetDailyChallenges: () => 
      dispatch({ type: 'RESET_DAILY_CHALLENGES' })
  };
};
