"use client";

import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';

interface NavigationItem {
  id: string;
  label: string;
  href: string;
  description: string;
}

const navigationItems: NavigationItem[] = [
  {
    id: 'home',
    label: 'Início',
    href: '/',
    description: 'Dashboard emocional'
  },
  {
    id: 'assessment',
    label: 'Avaliação',
    href: '/assessment',
    description: 'Autoavaliação emocional'
  },
  {
    id: 'challenges',
    label: 'Desafios',
    href: '/challenges',
    description: 'Desafios diários'
  },
  {
    id: 'map',
    label: 'Mapa',
    href: '/emotional-map',
    description: 'Mapa emocional'
  },
  {
    id: 'reflection',
    label: 'Reflexão',
    href: '/reflection',
    description: 'Reflexões guiadas'
  },
  {
    id: 'simulator',
    label: 'Simulador',
    href: '/social-simulator',
    description: 'Simulador social'
  },
  {
    id: 'sos',
    label: 'SOS',
    href: '/sos',
    description: 'Modo emergência'
  }
];

export default function AppNavigator() {
  const pathname = usePathname();

  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-white/95 backdrop-blur-md border-t border-gray-200 safe-area-bottom z-50">
      <div className="container-mobile">
        <div className="flex justify-between items-center py-2">
          {navigationItems.slice(0, 5).map((item) => {
            const isActive = pathname === item.href;
            
            return (
              <Link
                key={item.id}
                href={item.href}
                className={cn(
                  "flex flex-col items-center justify-center p-2 rounded-lg transition-all duration-200 min-w-[60px]",
                  isActive 
                    ? "text-blue-600 bg-blue-50" 
                    : "text-gray-600 hover:text-blue-500 hover:bg-gray-50"
                )}
              >
                <div className={cn(
                  "w-6 h-6 rounded-full mb-1 transition-all duration-200",
                  isActive 
                    ? "bg-gradient-to-r from-blue-400 to-blue-600" 
                    : "bg-gray-300"
                )} />
                <span className="text-xs font-medium">{item.label}</span>
              </Link>
            );
          })}
          
          {/* Menu dropdown para itens extras */}
          <div className="relative">
            <button className="flex flex-col items-center justify-center p-2 rounded-lg text-gray-600 hover:text-blue-500 hover:bg-gray-50 min-w-[60px]">
              <div className="w-6 h-6 rounded-full mb-1 bg-gray-300 flex items-center justify-center">
                <div className="w-1 h-1 bg-white rounded-full mx-0.5" />
                <div className="w-1 h-1 bg-white rounded-full mx-0.5" />
                <div className="w-1 h-1 bg-white rounded-full mx-0.5" />
              </div>
              <span className="text-xs font-medium">Mais</span>
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
}

// Componente para navegação superior (header)
export function TopNavigation({ title, showBack = false, onBack }: {
  title: string;
  showBack?: boolean;
  onBack?: () => void;
}) {
  return (
    <header className="sticky top-0 bg-white/95 backdrop-blur-md border-b border-gray-200 safe-area-top z-40">
      <div className="container-mobile">
        <div className="flex items-center justify-between h-16">
          {showBack && (
            <button
              onClick={onBack}
              className="flex items-center justify-center w-10 h-10 rounded-full hover:bg-gray-100 transition-colors"
            >
              <div className="w-6 h-6 border-l-2 border-b-2 border-gray-600 transform rotate-45" />
            </button>
          )}
          
          <h1 className="text-xl font-semibold text-gray-900 flex-1 text-center">
            {title}
          </h1>
          
          {showBack && <div className="w-10" />} {/* Spacer for centering */}
        </div>
      </div>
    </header>
  );
}

// Componente para navegação em abas
export function TabNavigation({ 
  tabs, 
  activeTab, 
  onTabChange 
}: {
  tabs: { id: string; label: string; count?: number }[];
  activeTab: string;
  onTabChange: (tabId: string) => void;
}) {
  return (
    <div className="flex bg-gray-100 rounded-lg p-1 mb-6">
      {tabs.map((tab) => (
        <button
          key={tab.id}
          onClick={() => onTabChange(tab.id)}
          className={cn(
            "flex-1 py-2 px-4 rounded-md text-sm font-medium transition-all duration-200",
            activeTab === tab.id
              ? "bg-white text-blue-600 shadow-sm"
              : "text-gray-600 hover:text-gray-900"
          )}
        >
          {tab.label}
          {tab.count !== undefined && (
            <span className={cn(
              "ml-2 px-2 py-0.5 rounded-full text-xs",
              activeTab === tab.id
                ? "bg-blue-100 text-blue-600"
                : "bg-gray-200 text-gray-600"
            )}>
              {tab.count}
            </span>
          )}
        </button>
      ))}
    </div>
  );
}

// Hook para navegação programática
export function useNavigation() {
  const pathname = usePathname();
  
  const getCurrentScreen = () => {
    const currentItem = navigationItems.find(item => item.href === pathname);
    return currentItem?.id || 'home';
  };
  
  const getScreenTitle = () => {
    const currentItem = navigationItems.find(item => item.href === pathname);
    return currentItem?.label || 'MindFlow';
  };
  
  return {
    currentScreen: getCurrentScreen(),
    currentTitle: getScreenTitle(),
    navigationItems
  };
}

// Componente para indicador de progresso de navegação
export function NavigationProgress({ currentStep, totalSteps }: {
  currentStep: number;
  totalSteps: number;
}) {
  const progress = (currentStep / totalSteps) * 100;
  
  return (
    <div className="w-full bg-gray-200 rounded-full h-2 mb-4">
      <div 
        className="bg-gradient-to-r from-blue-400 to-blue-600 h-2 rounded-full transition-all duration-300"
        style={{ width: `${progress}%` }}
      />
      <div className="flex justify-between mt-2 text-sm text-gray-600">
        <span>Passo {currentStep}</span>
        <span>{totalSteps} passos</span>
      </div>
    </div>
  );
}

// Componente para menu flutuante de ações rápidas
export function FloatingActionMenu({ 
  isOpen, 
  onToggle, 
  actions 
}: {
  isOpen: boolean;
  onToggle: () => void;
  actions: { id: string; label: string; onClick: () => void; color?: string }[];
}) {
  return (
    <div className="fixed bottom-20 right-4 z-50">
      {/* Actions */}
      {isOpen && (
        <div className="flex flex-col space-y-2 mb-4">
          {actions.map((action, index) => (
            <button
              key={action.id}
              onClick={action.onClick}
              className={cn(
                "px-4 py-2 rounded-full text-white font-medium shadow-lg transition-all duration-200 transform",
                action.color || "bg-blue-500 hover:bg-blue-600",
                isOpen ? "scale-100 opacity-100" : "scale-0 opacity-0"
              )}
              style={{ 
                transitionDelay: `${index * 50}ms` 
              }}
            >
              {action.label}
            </button>
          ))}
        </div>
      )}
      
      {/* Toggle button */}
      <button
        onClick={onToggle}
        className={cn(
          "w-14 h-14 rounded-full bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-lg transition-all duration-200 flex items-center justify-center",
          isOpen ? "rotate-45" : "rotate-0"
        )}
      >
        <div className="w-6 h-6 relative">
          <div className="absolute top-1/2 left-1/2 w-4 h-0.5 bg-white transform -translate-x-1/2 -translate-y-1/2" />
          <div className="absolute top-1/2 left-1/2 w-0.5 h-4 bg-white transform -translate-x-1/2 -translate-y-1/2" />
        </div>
      </button>
    </div>
  );
}
