"use client";

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Slider } from '@/components/ui/slider';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { useAppContext, useAppActions } from '@/context/AppContext';
import { TopNavigation } from '@/navigation/AppNavigator';
import { cn } from '@/lib/utils';

interface AssessmentQuestion {
  id: string;
  category: 'mood' | 'energy' | 'stress' | 'general';
  question: string;
  description: string;
  min: string;
  max: string;
}

interface AssessmentResponse {
  questionId: string;
  value: number;
  timestamp: Date;
}

const assessmentQuestions: AssessmentQuestion[] = [
  {
    id: 'mood',
    category: 'mood',
    question: 'Como você descreveria seu humor geral hoje?',
    description: 'Considere seus sentimentos predominantes ao longo do dia',
    min: 'Muito triste',
    max: 'Muito feliz'
  },
  {
    id: 'energy',
    category: 'energy',
    question: 'Qual é o seu nível de energia atual?',
    description: 'Pense na sua disposição física e mental',
    min: 'Exausto',
    max: 'Muito energético'
  },
  {
    id: 'stress',
    category: 'stress',
    question: 'Como você avalia seu nível de estresse?',
    description: 'Considere tensões, preocupações e pressões',
    min: 'Muito relaxado',
    max: 'Muito estressado'
  },
  {
    id: 'anxiety',
    category: 'general',
    question: 'Você se sente ansioso ou preocupado?',
    description: 'Pense em inquietações sobre o futuro ou situações específicas',
    min: 'Nada ansioso',
    max: 'Muito ansioso'
  },
  {
    id: 'confidence',
    category: 'general',
    question: 'Como está sua autoconfiança hoje?',
    description: 'Avalie sua confiança em suas habilidades e decisões',
    min: 'Sem confiança',
    max: 'Muito confiante'
  },
  {
    id: 'social',
    category: 'general',
    question: 'Como você se sente em relação às suas interações sociais?',
    description: 'Considere relacionamentos e conexões com outras pessoas',
    min: 'Isolado',
    max: 'Muito conectado'
  }
];

export default function EmotionalAssessmentScreen() {
  const { state } = useAppContext();
  const { updateEmotionalState, setLoading, setError } = useAppActions();
  
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [responses, setResponses] = useState<AssessmentResponse[]>([]);
  const [notes, setNotes] = useState('');
  const [isCompleted, setIsCompleted] = useState(false);
  const [showResults, setShowResults] = useState(false);

  const currentQuestion = assessmentQuestions[currentQuestionIndex];
  const progress = ((currentQuestionIndex + 1) / assessmentQuestions.length) * 100;

  // Encontra resposta atual
  const currentResponse = responses.find(r => r.questionId === currentQuestion.id);
  const currentValue = currentResponse?.value || 5;

  const handleValueChange = (value: number[]) => {
    const newResponse: AssessmentResponse = {
      questionId: currentQuestion.id,
      value: value[0],
      timestamp: new Date()
    };

    setResponses(prev => {
      const filtered = prev.filter(r => r.questionId !== currentQuestion.id);
      return [...filtered, newResponse];
    });
  };

  const handleNext = () => {
    if (currentQuestionIndex < assessmentQuestions.length - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
    } else {
      completeAssessment();
    }
  };

  const handlePrevious = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(prev => prev - 1);
    }
  };

  const completeAssessment = async () => {
    setLoading(true);
    setError(null);

    try {
      // Calcula valores principais
      const moodResponse = responses.find(r => r.questionId === 'mood');
      const energyResponse = responses.find(r => r.questionId === 'energy');
      const stressResponse = responses.find(r => r.questionId === 'stress');

      const newEmotionalState = {
        mood: moodResponse?.value || 5,
        energy: energyResponse?.value || 5,
        stress: stressResponse?.value || 5,
        timestamp: new Date()
      };

      // Simula salvamento no backend
      const saveData = {
        ...newEmotionalState,
        notes,
        tags: generateTags(responses),
        responses: responses.map(r => ({
          questionId: r.questionId,
          value: r.value
        }))
      };

      const response = await fetch('/api/userData', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(saveData),
      });

      if (!response.ok) {
        throw new Error('Erro ao salvar avaliação');
      }

      // Atualiza estado local
      updateEmotionalState(newEmotionalState);
      setIsCompleted(true);
      setShowResults(true);

    } catch (error) {
      console.error('Erro ao completar avaliação:', error);
      setError('Não foi possível salvar sua avaliação. Tente novamente.');
    } finally {
      setLoading(false);
    }
  };

  const generateTags = (responses: AssessmentResponse[]): string[] => {
    const tags: string[] = [];
    
    const anxietyResponse = responses.find(r => r.questionId === 'anxiety');
    const confidenceResponse = responses.find(r => r.questionId === 'confidence');
    const socialResponse = responses.find(r => r.questionId === 'social');

    if (anxietyResponse && anxietyResponse.value >= 7) tags.push('ansiedade');
    if (confidenceResponse && confidenceResponse.value <= 4) tags.push('baixa-autoestima');
    if (socialResponse && socialResponse.value <= 4) tags.push('isolamento');
    
    return tags;
  };

  const getValueDescription = (value: number, question: AssessmentQuestion) => {
    if (value <= 3) return question.min;
    if (value >= 8) return question.max;
    return 'Moderado';
  };

  const getValueColor = (value: number, category: string) => {
    if (category === 'stress') {
      return value <= 3 ? 'text-green-600' : value <= 6 ? 'text-yellow-600' : 'text-red-600';
    }
    return value >= 7 ? 'text-green-600' : value >= 4 ? 'text-yellow-600' : 'text-red-600';
  };

  if (showResults) {
    const moodValue = responses.find(r => r.questionId === 'mood')?.value || 5;
    const energyValue = responses.find(r => r.questionId === 'energy')?.value || 5;
    const stressValue = responses.find(r => r.questionId === 'stress')?.value || 5;

    return (
      <div className="min-h-screen bg-gradient-soft">
        <TopNavigation title="Resultado da Avaliação" />
        
        <div className="container-mobile py-6 space-y-6">
          <div className="text-center space-y-4">
            <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto">
              <div className="w-8 h-8 bg-green-500 rounded-full" />
            </div>
            <h1 className="text-2xl font-bold text-gray-900">
              Avaliação Concluída!
            </h1>
            <p className="text-gray-600">
              Obrigado por compartilhar como você está se sentindo
            </p>
          </div>

          <Card className="card-elevated">
            <CardHeader>
              <CardTitle>Seu Estado Emocional</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-3 gap-4 text-center">
                <div>
                  <div className={cn("text-2xl font-bold", getValueColor(moodValue, 'mood'))}>
                    {moodValue}/10
                  </div>
                  <div className="text-sm text-gray-600">Humor</div>
                </div>
                <div>
                  <div className={cn("text-2xl font-bold", getValueColor(energyValue, 'energy'))}>
                    {energyValue}/10
                  </div>
                  <div className="text-sm text-gray-600">Energia</div>
                </div>
                <div>
                  <div className={cn("text-2xl font-bold", getValueColor(stressValue, 'stress'))}>
                    {stressValue}/10
                  </div>
                  <div className="text-sm text-gray-600">Estresse</div>
                </div>
              </div>

              {notes && (
                <div className="pt-4 border-t">
                  <h4 className="font-medium text-gray-900 mb-2">Suas Observações:</h4>
                  <p className="text-sm text-gray-600 bg-gray-50 p-3 rounded-lg">
                    {notes}
                  </p>
                </div>
              )}
            </CardContent>
          </Card>

          <div className="space-y-3">
            <Button 
              className="w-full btn-primary"
              onClick={() => window.location.href = '/'}
            >
              Voltar ao Início
            </Button>
            <Button 
              variant="outline" 
              className="w-full"
              onClick={() => window.location.href = '/emotional-map'}
            >
              Ver Histórico Emocional
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-soft">
      <TopNavigation 
        title="Autoavaliação" 
        showBack={true}
        onBack={() => window.history.back()}
      />
      
      <div className="container-mobile py-6 space-y-6">
        {/* Progress */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm text-gray-600">
            <span>Pergunta {currentQuestionIndex + 1} de {assessmentQuestions.length}</span>
            <span>{Math.round(progress)}%</span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-2">
            <div 
              className="bg-gradient-to-r from-blue-400 to-blue-600 h-2 rounded-full transition-all duration-300"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>

        {/* Error Alert */}
        {state.error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">
              {state.error}
            </AlertDescription>
          </Alert>
        )}

        {/* Question Card */}
        <Card className="card-elevated">
          <CardHeader>
            <div className="flex items-center justify-between">
              <Badge variant="outline" className="text-xs">
                {currentQuestion.category}
              </Badge>
              <Badge variant="secondary" className="text-xs">
                {currentQuestionIndex + 1}/{assessmentQuestions.length}
              </Badge>
            </div>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="space-y-2">
              <h2 className="text-xl font-semibold text-gray-900">
                {currentQuestion.question}
              </h2>
              <p className="text-sm text-gray-600">
                {currentQuestion.description}
              </p>
            </div>

            {/* Slider */}
            <div className="space-y-4">
              <div className="px-2">
                <Slider
                  value={[currentValue]}
                  onValueChange={handleValueChange}
                  max={10}
                  min={1}
                  step={1}
                  className="w-full"
                />
              </div>
              
              <div className="flex justify-between text-xs text-gray-500">
                <span>{currentQuestion.min}</span>
                <span>{currentQuestion.max}</span>
              </div>

              <div className="text-center">
                <div className={cn(
                  "text-2xl font-bold mb-1",
                  getValueColor(currentValue, currentQuestion.category)
                )}>
                  {currentValue}/10
                </div>
                <div className="text-sm text-gray-600">
                  {getValueDescription(currentValue, currentQuestion)}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Notes (only on last question) */}
        {currentQuestionIndex === assessmentQuestions.length - 1 && (
          <Card className="card-soft">
            <CardHeader>
              <CardTitle className="text-lg">Observações Adicionais</CardTitle>
            </CardHeader>
            <CardContent>
              <Textarea
                placeholder="Compartilhe qualquer observação sobre como você está se sentindo hoje... (opcional)"
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                className="min-h-[100px] resize-none"
              />
            </CardContent>
          </Card>
        )}

        {/* Navigation */}
        <div className="flex space-x-3">
          <Button
            variant="outline"
            onClick={handlePrevious}
            disabled={currentQuestionIndex === 0}
            className="flex-1"
          >
            Anterior
          </Button>
          <Button
            onClick={handleNext}
            disabled={state.loading}
            className="flex-1 btn-primary"
          >
            {state.loading ? 'Salvando...' : 
             currentQuestionIndex === assessmentQuestions.length - 1 ? 'Finalizar' : 'Próxima'}
          </Button>
        </div>

        {/* Question indicators */}
        <div className="flex justify-center space-x-2">
          {assessmentQuestions.map((_, index) => (
            <div
              key={index}
              className={cn(
                "w-2 h-2 rounded-full transition-all duration-200",
                index === currentQuestionIndex ? "bg-blue-500" :
                index < currentQuestionIndex ? "bg-green-500" : "bg-gray-300"
              )}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
